.link {
  color: inherit;
  text-decoration: none;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, p {
  margin-top: 0;
  margin-bottom: 0;
}

:before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

html {
  scroll-behavior: smooth;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

button {
  cursor: pointer;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 16px;
  }
}

:root {
  --accent-font-family: "Titan One";
  --second-font-family: "DM Sans", sans-serif;
  --accent-text-color: #d41443;
  --background-accent-button-color: #d41342;
  --middle-accent-text-color: #ffa5ba;
  --main-text-color: #fff;
  --description-title-color: #000;
  --description-text-color: #907e82;
  --background-main-color: #ffb8ca;
  --background-middle-color: #fff5f6;
  --border-down-color: #e1e1e1;
  --social-icon-color: #fea5bb;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
}

.big-button {
  text-align: center;
  min-width: 161px;
  min-height: 44px;
  border: none;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  padding: 10px 17px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3;
  display: flex;
}

@media screen and (min-width: 768px) {
  .big-button {
    min-width: 191px;
  }
}

@media screen and (min-width: 1200px) {
  .big-button {
    min-width: 198px;
  }
}

.big-button--primary {
  color: var(--main-text-color);
  background-color: var(--background-accent-button-color);
  transition: color 250ms var(--timing-function), background-color 250ms var(--timing-function);
  margin-left: auto;
  margin-right: auto;
}

.big-button--primary:hover, .big-button--primary:focus {
  color: var(--accent-text-color);
  background-color: #fff;
}

.big-button--primary > .big-buttom__icon {
  fill: currentColor;
}

.big-button--secondary {
  color: var(--accent-text-color);
  transition: color 250ms var(--timing-function), background-color 250ms var(--timing-function);
  background-color: #fff;
  margin-left: auto;
  margin-right: auto;
}

.big-button--secondary:hover, .big-button--secondary:focus {
  color: #fff;
  background-color: var(--accent-text-color);
}

.big-button--secondary > .big-buttom__icon {
  fill: currentColor;
}

.big-buttom__icon {
  margin-left: 8px;
}

