.link {
    text-decoration: none;
    color: inherit;
}

.list {
    padding: 0;
    margin: 0;

    list-style: none;

}

h1,
h2,
h3,
p {
    margin-top: 0;
    margin-bottom: 0;
}

*::before,
*::after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
    list-style: none;
}

img {
	display: block;
	width: 100%;
	height: auto;
}

html {
	scroll-behavior: smooth;
	}